.containerStyle {
    width: 65%;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f0f0f0;
    text-align: center;
    padding: 20px;
    overflow: hidden; /* 자식 요소를 감싸도록 함 */
}

.leftContent {
    float: left; /* 왼쪽에 위치시킴 */
}

.rightContent {
    float: right; /* 오른쪽에 위치시킴 */
}

.leftContent, .rightContent {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    text-align: left;
    height: 100%; /* 기준 요소의 높이를 상속 */
}

.mainContent {
    display: inline-block; /* 인라인 블록 요소로 설정하여 같은 라인에 위치 */
    vertical-align: top; /* 상단 정렬 */
    width: 100%; /* 전체 너비를 사용하여 같은 라인에 위치 */
    text-align: center; /* 텍스트 가운데 정렬 */
}

.imgStyle {
    max-width: 100px;
    height: auto;
    display: block;
    margin: 0 auto;
}
